//清空包含数组和嵌套对象的对象
export function clearForm(obj) {
    for (let key in obj) {
      if (typeof obj[key] === 'object' && obj[key] !== null) {
        // 如果值是对象，则递归清空对象内部的键值对
        clearForm(obj[key]);
      } else if (Array.isArray(obj[key])) {
        // 如果值是数组，则将数组清空为一个空数组
        obj[key] = [];
      } else {
        // 否则，将键的值置为空
        obj[key] = '';
      }
    }
}

//生成随机数
export function randomNum(len, radix) {
  const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('')
  const uuid = []
  radix = radix || chars.length

  if (len) {
    // Compact form
    for (let i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix ]
  } else {
    // rfc4122, version 4 form
    let r

    // rfc4122 requires these characters
    uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-'
    uuid[14] = '4'

    // Fill in random data.  At i==19 set the high bits of clock sequence as
    // per rfc4122, sec. 4.1.5
    for (let i = 0; i < 36; i++) {
      if (!uuid[i]) {
        r = 0 | Math.random() * 16
        uuid[i] = chars[(i === 19) ? (r & 0x3) | 0x8 : r]
      }
    }
  }
  return uuid.join('') + new Date().getTime()
}

//获取给定时间戳的对应日期
export function getDateTime(time) {
  const dt = new Date(time * 1000);
  const year = dt.getFullYear();
  const month = String(dt.getMonth() + 1).padStart(2, '0');
  const day = String(dt.getDate()).padStart(2, '0');
  const hours = String(dt.getHours()).padStart(2, '0');
  const minutes = String(dt.getMinutes()).padStart(2, '0');
  const seconds = String(dt.getSeconds()).padStart(2, '0');
  return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
}

//二维数组翻转
export function transposeMatrix(matrix) {
  // 获取原始二维数组的行数和列数
  const rows = matrix.length;
  const cols = matrix[0].length;

  // 创建一个新的二维数组来存放翻转后的结果
  const transposedMatrix = [];

  // 初始化翻转后的二维数组，将行数和列数互换
  for (let i = 0; i < cols; i++) {
    transposedMatrix.push([]);
  }

  // 遍历原始二维数组，将行与列交换，得到翻转后的结果
  for (let i = 0; i < rows; i++) {
    for (let j = 0; j < cols; j++) {
      transposedMatrix[j][i] = matrix[i][j];
    }
  }

  return transposedMatrix;
}

//通过dataType返回对应的值
export function getValueByType(obj) {
  switch (obj.dataType) {
    case 1:
      return obj.dataBool;
    case 3:
    case 4:
    case 5:
      return obj.dataInt;
    case 6:
      return obj.dataReal32;
    case 7:
      return obj.dataReal64;
    case 8:
      return obj.dataText;
    case 9:
      return obj.dataBlob;
    case 10:
      return obj.dataGeometry;
    case 11:
      return obj.dataTime;
    default:
      return null;
  }
}

//目标表类型与mdb类型映射(根据fieldType映射dataType)
export function mdbMapping(fieldType){
  switch(fieldType){
    case 0:
      return 3;
    case 1:
      return 4;
    case 2:
      return 5;
    case 3:
      return 6; 
    case 4:
      return 7; 
    case 5: 
      return 1;
    case 6:
    case 7:
      return 2;
    case 8:
      return 8;
    case 9:
      return 11;
    case 10:
      return 9;
    case 11:
      return 10;
    case 12:
      return 5;
    case 13:
      return 8;
    default:
      return 0;
  }
}