import * as NewsManagerGrpc from "../../assets/proto/news/news_grpc_web_pb"

export default {
    getNewsContent(pageNo,pageSize){
        let client = new NewsManagerGrpc.NewsManagerPromiseClient(
            process.env.VUE_APP_HZ_NEWS_URL
        );
        let page = new NewsManagerGrpc.Pagination();
        page.setPageNo(pageNo);
        page.setPageSize(pageSize);
        let ListNewsRequest = new NewsManagerGrpc.ListNewsRequest();
        ListNewsRequest.setPagination(page);
        let res = client.listNews(ListNewsRequest, {});
        return res;
    }
}